body {
    display: grid;
    place-items: center; 
    min-height: 100vh;
    margin: 0;
}
.calendar-container {
    width: 300px;
    border: 1px solid #ccc;
    font-family: sans-serif;
}

.days-of-week {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    
}

.day-of-week{
    text-align: center;
    padding: 5px;
}


.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
}

.calendar-day {
    padding: 10px;
    text-align: center;
    border: 1px solid #eee;
}

input[type="month"] {
    margin-bottom: 10px;
}
